import React, { useEffect, useState } from 'react';
import { useBattery } from '@uidotdev/usehooks';

function BatteryInfo() {
  const { loading, level, charging, chargingTime, dischargingTime } = useBattery();

  if(loading){
    return <div>Loading...</div>
  }

  let chargeTime = Math.trunc(chargingTime / 60);
  let dischargeTime = Math.trunc(dischargingTime / 60);
  let chargeHours = Math.trunc(chargeTime / 60);
  let dischargeHours = Math.trunc(dischargeTime / 60);
  let calculatingBattery = false;

  return(
    <div>
        <p>Battery Level: {(level*100).toFixed(0)}%</p>
        <p>Charging: {charging ? 'Yes' : 'No'}</p>
        {dischargingTime === Infinity ?
        <p></p>
        :
        <p>Discharging Time: {dischargeHours > 0 ? `${dischargeHours} hour(s) ${dischargeTime - (dischargeHours * 60)} minute(s)` : `${dischargeTime} minute(s)`}</p>
        }
        {chargingTime === Infinity ?
        <p></p>
        :
        <p>Charging Time: {chargeHours > 0 ? `${chargeHours} hour(s) ${chargeTime - (chargeHours * 60)} minute(s)` : `${chargeTime} minute(s)`}</p>
        }


        
    </div>
  )
}

export default BatteryInfo;